// -------------------------------------------------------------------------------
//
// Description: Normalize.css makes browsers render all elements more consistently
// and in line with modern standards. It precisely targets only the styles that
// need normalizing.
// ===============================================================================
//

// Import normalize from node modules
@import './../../../node_modules/normalize-scss/sass/normalize.scss';

// Output CSS rules with normalize mixin
@include normalize();

// Set global box model
*,
*::before,
*::after {
    box-sizing: border-box;
}
