// -------------------------------------------------------------------------------
//
// Description: This mixin takes a parameter for typographic scale (sass-map: $buf-type-scale).
// It then uses the function map-fetch to reach deep key:value pairs within this map.
// 
// Finally it outputs the correct pairing of font-size and line-height, using the
// compass-mixin `buf-type-scale`.
// ===============================================================================
//

@mixin buf-type-scale($scale, $map: $buf-type-scale) {
  $font-size: map-fetch($map, $scale font-size);
  $base-lines: map-fetch($map, $scale base-lines);
    
  font-size: rem($font-size);
  line-height: rem($base-lines * $buf-base-unit);
}

// Example usage
// -------------------------------------------------------------------------------
// .class-name {
//   @include buf-type-scale(base);
// }
