// -------------------------------------------------------------------------------
//
// Description: Align text
// ===============================================================================
//

.buf-text-start {
  @include buf-text-align(start);
}

.buf-text-end {
  @include buf-text-align(end);
}

.buf-text-left {
  @include buf-text-align(left);
}

.buf-text-right {
  @include buf-text-align(right);
}

.buf-text-center {
  @include buf-text-align(center);
}

.buf-text-justify {
  @include buf-text-align(justify);
}

.buf-text-justify-all {
  @include buf-text-align(justify-all);
}

.buf-text-match-parent {
  @include buf-text-align(match-parent);
}
