// Headers
// -------------------------------------------------------------------------------
%buf-header-base {
  @include buf-font-weight($regular);
  @include buf-font-family(base);
}

h1,
.buf-h1 {
  @extend %buf-header-base;
  @include buf-type-scale(xxlarge);
}

h2,
.buf-h2 {
  @extend %buf-header-base;
  @include buf-type-scale(xlarge);
}

h3,
.buf-h3 {
  @extend %buf-header-base;
  @include buf-type-scale(large);
}

h4,
.buf-h4 {
  @extend %buf-header-base;
  @include buf-type-scale(medium);
}

h5,
.buf-h5 {
  @extend %buf-header-base;
  @include buf-type-scale(base);
}

h6,
.buf-h6 {
  @extend %buf-header-base;
  @include buf-type-scale(small);
}

