// -------------------------------------------------------------------------------
//
// Description: Set icon with and height
// ===============================================================================
//

@mixin buf-icon-scale($scale, $map: $buf-icon-scale) {
  width: rem-calc(map-fetch($map, $scale width));
  height: rem-calc(map-fetch($map, $scale height));
}

// Example usage
// -------------------------------------------------------------------------------
// .class-name {
//   @include buf-icon-scale(base);
// }
