// -------------------------------------------------------------------------------
//
// Description: Base icon styling
// ===============================================================================
//

.buf-icon {
  display: block;
}

.buf-icon--inline {
  display: inline-block;
}

.buf-icon--tiny {
  @include buf-icon-scale(tiny);
}

.buf-icon--mini {
  @include buf-icon-scale(mini);
}

.buf-icon--small {
  @include buf-icon-scale(small);
}

.buf-icon--base {
  @include buf-icon-scale(base);
}

.buf-icon--medium {
  @include buf-icon-scale(medium);
}

.buf-icon--large {
  @include buf-icon-scale(large);
}

.buf-icon--xlarge {
  @include buf-icon-scale(xlarge);
}

.buf-icon--xxlarge {
  @include buf-icon-scale(xxlarge);
}

.ch-icon--light {
  fill: $color-white;
}

.ch-icon--dark {
  fill: $color-black;
}
