// -------------------------------------------------------------------------------
//
// Description: Adjust font and line-height values
// ===============================================================================
//

@mixin buf-establish-baseline {
  font-size: $buf-base-font-size;
  line-height: $buf-base-line-height;
  text-size-adjust: 100%;
}

