// -------------------------------------------------------------------------------
//
// Description: Convert px to rem
// ===============================================================================
//

@function rem($pixels, $context: $buf-base-font-size) {
  @if (unitless($pixels)) {
    $pixels: $pixels * 1px;
  }
  
  @if (unitless($context)) {
    $context: $context * 1px;
  }
  
  @return $pixels / $context * 1rem;
}

// Example usage
// -------------------------------------------------------------------------------
// .class-name {
//   padding: rem(16px); // converts to: padding: 1rem;
// }
